<template>
  <header class="header">

    <!--identity-->
    <div class="identity">
      <img height="27" src="../../../assets/artemis.jpg" alt="Artemis"/>
    </div>

    <div class="actions" v-if="true">
      <!--user-->
      <dropdown menu-right>

        <div class="user" data-role="trigger" >
          <!-- <span>{{this.$store.getters['auth/getUserName']}}</span> -->
          <i class="fa fa-chevron-down"></i>
        </div>

        <template slot="dropdown">
          <li><a role="button"><i class="icon-settings icons icon-margin"></i>User Settings</a></li>
          <li><a role="button" @click="logout" href="/"><i class="icon-logout icons icon-margin"></i>Logout</a></li>
        </template>

      </dropdown>

    </div>

  </header>
</template>

<script>
  import { Dropdown } from 'uiv'
  // import { mapGetters } from 'vuex'

  export default {
    name: 'app-header',
    components: { Dropdown },
    data () {
      return {
        apiKey: ''
      }
    },
    methods: {
      logout () {
        return this.auth0Logout()
      }
    }
  }
</script>

<style lang="less" scoped>

  @import (reference) '../../styles/app-helper.less';

  .header {
    padding: .5rem 1.3rem;
    background-color: white;

    display: flex;
    justify-content: space-between;
    align-items: center;

    // .box-shadow(0 1px 4px fadeout(black, 80%));
    border-bottom: solid thin @grey4;

    .identity {
      flex: 0 0 auto;
      margin-top: 4px;
    }

    .actions {
      flex: 0 0 auto;

      display: flex;
      align-items: center;

      .search {
        flex: 0 0 auto;
        margin-right: 1rem;
        display: flex;
      }

      .user {
        flex: 0 0 auto;

        display: flex;
        align-items: center;

        padding: .2rem .4rem;
        cursor: default;

        span {
          font-size: 1.1rem;
          margin-right: .5rem;
        }

        i {
          font-size: .6rem;
        }
      }

    }
  }
</style>



