import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/pages/common/Login'
import Home from '@/pages/Home.vue'
import About from '@/components/About.vue'
import NotFound from '@/components/NotFound.vue'


const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
    meta: {
      navBarLink: true,
      hasNav: true,
      showHeader: true,
      icon: 'home'
    }

  },
  {
    path: '/about',
    component: About,
    name: 'about',
    meta: {
      navBarLink: true,
      icon: 'info'
    }
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {
      navBarLink: false,
      hasNav: false,
      navBarTitle: 'Login'
    }
  },
  {
    path: '*',
    component: NotFound,
    meta: {
      hasNav: false
    }
  }
]


Vue.use(Router)

export default new Router({
  routes,
   mode: 'history'
})


