<template>
  <div class="header" :class="`${layout}-layout`">
    <div class="page-header-content">

      <!-- title -->
      <div class="page-title-container">
        <h2 class="title">{{title}}</h2>
        <h4 class="sub-title">{{subTitle}}</h4>
      </div>

      <!-- actions -->
      <div class="page-actions" v-if="this.$slots['page-actions']">
        <slot name="page-actions"/>
      </div>

    </div>

    <!--header tabs-->
    <portal-target name="page-header-tabs" class="page-header-links" v-if="hasTabs"></portal-target>

  </div>
</template>

<script>
  export default {
    name: 'app-header',
    props: {
      title: {
        type: String,
        default () {
          return this.$route.meta.navBarTitle
        }
      },
      subTitle: {
        type: String,
        default: ''
      },
      hasTabs: {
        type: Boolean,
        default: false
      },
      layout: {
        type: String,
        default: 'search'
      }
    }
  }
</script>

<style lang="less" scoped>
  @import (reference) '../../styles/app-helper.less';

  .header {
    width: 100%;
    top: 0;
    padding: 1.7rem 0 0; margin: 0;

    background-color: @page-header-color;
    border-bottom: solid 1px darken(@page-header-color, 5%);
    overflow: hidden;

    .page-header-content {
      width: 100%; height: 100%;
      //.max-page-width;

      padding: 0 @page-padding;

      .page-title-container {
        height: 100%;
        padding: 0; margin: 0;

        .title {
          color: @c-inverse;
          font-weight: @bold;
          font-size: 1.4rem;
        }

        .sub-title {
          color: @c-inverse;
          font-weight: @regular;
          font-size: 1.1rem;
        }
      }

      .page-actions {
        width: 100%; height: auto;
        padding: 1.5rem 0 1.3rem;
      }
    }

    &.empty-layout {
      padding: 1.2rem 0;
    }

    &.advanced-layout {
      padding: 1.5rem 2rem;

      .page-header-content {
        padding: 0;
      }
      .page-actions {
        padding: 0;
      }
    }

    &.buttons-layout {
      padding-bottom: 1.2rem;

      .page-header-content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: .5em;
        padding-bottom: .3em;

        .page-title-container {
          flex: 0 0 auto;
        }

        .page-actions {
          flex: 0 1 auto;
          width: auto;
          padding: 0;

          button {
            margin-left: .5rem;
            float: right;
          }
        }
      }
    }

  }


</style>



