import router from '@/router'

export default {

  namespaced: true,

  state: {
    user: null,
    id_token: null,
    access_token: null,
    expires_at: null
  },

  getters: {
    getToken: state => state.id_token,
    getUserName: state => state.user.name,
    getUser: state => state.user,
    getExpiresAt: state => state.expires_at
  },

  mutations: {

    setIdToken (state, token) {
      state.id_token = token
    },

    setAccessToken (state, token) {
      state.access_token = token
    },

    setExpiresAt (state, time) {
      state.expires_at = time
    },

    setUser (state, user) {
      state.user = user
    },

    clearAuth (state) {
      state.id_token = null
      state.user = null
      state.access_token = null
      state.expires_at = null
    }

  },

  actions: {

    login (context, args) {
      context.commit('setToken', args.token)
      context.commit('setUser', args.username)

      router.push({name: 'patient'})
    },

    logout (context) {
      context.commit('clearAuth')

      router.push({name: 'login'})
    }

  }

}



