<template>
   <div class="mainComponents">
       <!-- <div class="events">

       </div>
       <div class="feed">
           <feed></feed>
       </div> -->
       <p>Random num from backend: {{randomNumber}}</p>
        <button @click="getRandom">New random number</button>
   </div>
</template>

<script>
    import Feed from '@/components/Feed.vue'
    import AppPageHeader from '@/components/partials/AppPageHeader'
    import axios from 'axios'

    export default {
        data () {
            return {
                randomNumber: 0
            }
        },
        methods: {
            getRandomFromBackend () {
                const path = `http://localhost:5000/api/random`

                axios.get(path)
                .then(response => {
                    this.randomNumber = response.data.randomNumber
                })
                .catch(error => {
                console.log(error)
                })
            },
            getRandom () {
                this.randomNumber = this.getRandomFromBackend()
            }
        },
        created () {
            this.getRandom()
        }, 

        name: 'Home',
        components: {
            Feed
        },
    }
</script>
<style>
    .mainComponents{
        background-color: grey;
        height:100vw;
        width:100%;
    }
    
    .feed {
        margin-top: 3px;
    }
</style>



